import React, { Component } from 'react';
import PropTypes from 'prop-types';

export default class RebatesPopupNonActivate extends Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        this.button.addEventListener("click", () => {
            this.props.onActivate();
        });
    }

    render() {
        const button_style = {
            width: "300px",
            boxSizing: "border-box",
            backgroundColor: "#BF0000",
            border: "none",
            cursor: "pointer",
            color: "#ffffff",
            fontWeight: "bold",
            fontSize: "12px",
            lineHeight: "100%",
            height : "30px",
            borderRadius:"3px",
            textIndent: "0",
            boxShadow: "#970000 0px 3px",
            letterSpacing: "0.6px",
            fontFamily: "'','Noto Sans', 'Noto Sans CJK JP', ' Pro W3', 'Hiragino Kaku Gothic Pro', '', 'Meiryo', 'Osaka', '', 'Yu Gothic', sans-serif",
        };

        return (
            <button style={button_style}
                    ref={c => this.button = c}>{this.props.rebates_info.comment}GET!</button>
        );
    }
}

RebatesPopupNonActivate.propTypes = {
    rebates_info: PropTypes.object,
    onActivate: PropTypes.func,
};
